  color: #fff;
}

.single__step__item span i {
  color: #fff;
  padding: 15px;
  font-size: 1.5rem;
  background: #5142fc;
  border-radius: 5px;
}

.step__item__content {
  margin-top: 20px !important;
}

@media only screen and (max-width: 992px) {
